import React, { useEffect, useState } from 'react';
import './style.css';
import { usePagination } from 'hooks';

interface Place {
    id: string;
    place_name: string;
    x: string;
    y: string;
    address_name: string;
    road_address_name?: string;
    [key: string]: any; //     
}

export default function NEARBY() {
    const [map, setMap] = useState<any>(null);
    const [places, setPlaces] = useState<any[]>([]);
    const [activeInfowindows, setActiveInfowindows] = useState<any[]>([]); //    
    const [currentLatLng, setCurrentLatLng] = useState<{ lat: number, lng: number } | null>(null);
    const [keyword, setKeyword] = useState<string>(''); //  
    const [categoryCode, setCategoryCode] = useState<string>(''); //    
    const [markers, setMarkers] = useState<any[]>([]); //   

    const {
        currentPage,
        setCurrentPage,
        currentSection,
        setCurrentSection,
        viewList,
        viewPageList,
        totalSection,
        setTotalList,
    } = usePagination<any>(8); //  8 

    useEffect(() => {
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(
                (position) => {
                    const lat = position.coords.latitude;
                    const lng = position.coords.longitude;
                    setCurrentLatLng({ lat, lng });
                    initializeMap(lat, lng);
                    searchCategoryPlaces(lat, lng, 'CE7'); //    
                },
                (error) => {
                    console.error('Geolocation error:', error);
                    initializeMap(37.5665, 126.9780); //  
                }
            );
        } else {
            console.error('Geolocation is not supported by this browser.');
            initializeMap(37.5665, 126.9780); //  
        }
    }, []);

    const initializeMap = (lat: number, lng: number) => {
        const container = document.getElementById('map');
        const options = {
            center: new window.kakao.maps.LatLng(lat, lng),
            level: 5,
        };

        const mapInstance = new window.kakao.maps.Map(container, options);
        setMap(mapInstance);

        const markerPosition = new window.kakao.maps.LatLng(lat, lng);
        const marker = new window.kakao.maps.Marker({
            position: markerPosition,
            map: mapInstance,
        });

        const infowindow = new window.kakao.maps.InfoWindow({
            content: createInfoWindowContent('  ', '  !'),
            removable: true, //   
        });

        infowindow.open(mapInstance, marker);
        setActiveInfowindows([infowindow]); //     
        setMarkers([marker]); //     
    };

    const handleKeywordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setKeyword(e.target.value);
    };

    const handleKeyPress = (e: React.KeyboardEvent<HTMLInputElement>) => {
        if (e.key === 'Enter') {
            searchKeywordPlaces();
        }
    };

    const searchKeywordPlaces = () => {
        if (!map || !currentLatLng) return;

        const ps = new window.kakao.maps.services.Places();

        ps.keywordSearch(keyword, (data: any[], status: string) => {
            if (status === window.kakao.maps.services.Status.OK) {
                setPlaces(data);
                setTotalList(data);
                closeAllInfowindows(); //   
                removeAllMarkers(); //   
                displayMarkers(data.slice(0, 8)); //  8  
            }
        }, {
            location: new window.kakao.maps.LatLng(currentLatLng.lat, currentLatLng.lng),
            radius: 500, // 500m   
        });
    };

    const searchCategoryPlaces = (lat: number, lng: number, categoryGroupCode: string) => {
        if (!map) return;

        setCategoryCode(categoryGroupCode); //     

        const ps = new window.kakao.maps.services.Places();

        const callback = (data: any[], status: string) => {
            if (status === window.kakao.maps.services.Status.OK) {
                setPlaces(data);
                setTotalList(data);
                closeAllInfowindows(); //   
                removeAllMarkers(); //   
                displayMarkers(data.slice(0, 8)); //  8  
            }
        };

        ps.categorySearch(categoryGroupCode, callback, {
            location: new window.kakao.maps.LatLng(lat, lng),
            radius: 500, // 500m   
        });
    };

    const displayMarkers = (places: any[]) => {
        //   
        removeAllMarkers();
        closeAllInfowindows(); //   

        const bounds = new window.kakao.maps.LatLngBounds();
        const newMarkers: any[] = []; //    
        const newInfowindows: any[] = []; //    

        places.forEach((place: any) => {
            const position = new window.kakao.maps.LatLng(place.y, place.x);
            const marker = new window.kakao.maps.Marker({
                position: position,
                map: map,
            });

            const infowindow = new window.kakao.maps.InfoWindow({
                content: createInfoWindowContent(place.place_name, place.road_address_name || place.address_name),
                removable: true,
            });

            window.kakao.maps.event.addListener(marker, 'click', () => {
                closeAllInfowindows(); //     
                infowindow.open(map, marker);
                setActiveInfowindows([infowindow]); //    
            });

            newMarkers.push(marker); //   
            newInfowindows.push(infowindow); //   
            bounds.extend(position);
        });

        map.setBounds(bounds);
        setMarkers(prevMarkers => [...prevMarkers, ...newMarkers]); //    
        setActiveInfowindows(newInfowindows); //    
    };

    const createInfoWindowContent = (title: string, subtitle: string) => {
        return `
            <div class="infowindow-content">
                <div class="infowindow-title">${title}</div>
                <div class="infowindow-subtitle">${subtitle}</div>
            </div>
        `;
    };

    const handleGoToCurrentLocation = () => {
        if (currentLatLng) {
            const { lat, lng } = currentLatLng;
            initializeMap(lat, lng);
            if (categoryCode) {
                searchCategoryPlaces(lat, lng, categoryCode);
            } else {
                searchKeywordPlaces();
            }
        }
    };

    const goToPrevPage = () => {
        if (currentPage > 1) {
            const newPage = currentPage - 1;
            setCurrentPage(newPage);
            displayMarkers(viewList);
        }
    };

    const goToNextPage = () => {
        const lastPage = Math.ceil(places.length / 8);
        if (currentPage < lastPage) {
            const newPage = currentPage + 1;
            setCurrentPage(newPage);
            displayMarkers(viewList);
        }
    };

    const closeAllInfowindows = () => {
        activeInfowindows.forEach(infowindow => infowindow.close());
        setActiveInfowindows([]); //     
    };

    const removeAllMarkers = () => {
        markers.forEach(marker => marker.setMap(null));
        setMarkers([]); //     
    };

    return (
        <div id='nearby-wrapper'>
            <div className='nearby-container'>
                <div className="map-container">
                    <button onClick={handleGoToCurrentLocation} className="go-to-current-location"> </button>
                    <div id="map" />
                </div>
                <div className="search-container">
                    <div className='nearby-title'>
                        {'  \n How?Se!'}
                    </div>

                    <div style={{ marginBottom: '10px' }}>
                        <input
                            type="text"
                            value={keyword}
                            onChange={handleKeywordChange}
                            onKeyDown={handleKeyPress}
                            placeholder=" "
                        />
                        <button onClick={searchKeywordPlaces}></button>
                    </div>
                    <div style={{ marginBottom: '10px' }}>
                        <button onClick={() => searchCategoryPlaces(map.getCenter().getLat(), map.getCenter().getLng(), 'CE7')}>
                             
                        </button>
                        <button onClick={() => searchCategoryPlaces(map.getCenter().getLat(), map.getCenter().getLng(), 'FD6')}>
                             
                        </button>
                    </div>
                    <div id="result-list" className="result-list">
                        <ul>
                            {viewList.map((place) => (
                                <li key={place.id} onClick={() => displayMarkers([place])}>
                                    {place.place_name}
                                </li>
                            ))}
                        </ul>
                    </div>
                    <div className="pagination">
                        <button onClick={goToPrevPage} disabled={currentPage === 1}></button>
                        <button onClick={goToNextPage} disabled={currentPage === viewPageList.length}></button>
                    </div>
                </div>
            </div>
        </div>
    );
}
